import React, { lazy, Suspense, Component } from "react";
import { Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import routes from "../routes";
import { getIsAutentificated } from "../redux/auth/auth-selectors";
import styles from "./app.module.css";
import AuthNav from "./AuthNav";
import Nav from "./Nav";
import UserMenu from "./UserMenu";
import { getCurrent } from "../redux/auth/auth-operations";
import PrivateRoute from "./PrivateRoute";
import PublicRoute from "./PublicRoute";

const HomePage = lazy(() =>
  import("../views/HomePage" /* webpackChunkName: "home-page" */)
);

const PhoneBook = lazy(() =>
  import("../views/PhoneBook" /* webpackChunkName: "phonebook" */)
);

const Registration = lazy(() =>
  import("../views/Registration" /* webpackChunkName: "registration" */)
);

const Login = lazy(() =>
  import("../views/Login" /* webpackChunkName: "login" */)
);

const Error = lazy(() =>
  import("../views/Error" /* webpackChunkName: "login" */)
);

class App extends Component {
  componentDidMount() {
    this.props.onPageRefresh();
  }

  render() {
    return (
      <>
        <header className={styles.header}>
          <Nav />
          {this.props.isAuthenticated ? <UserMenu /> : <AuthNav />}
        </header>

        <Suspense>
          <Switch fallback={<p>Loading...</p>}>
            <Route path={routes.home} exact component={HomePage} />
            <PrivateRoute
              path={routes.phonebook}
              component={PhoneBook}
              redirectTo={routes.home}
            />
            <PublicRoute
              path={routes.registration}
              restricted
              component={Registration}
              redirectTo={routes.home}
            />
            <PublicRoute
              path={routes.login}
              restricted
              component={Login}
              redirectTo={routes.home}
            />
            <Route component={Error} />
          </Switch>
        </Suspense>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  isAuthenticated: getIsAutentificated(state),
});

const mapDispatchToProps = {
  onPageRefresh: getCurrent,
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
